@use '../base/mixins' as m;
.Holder-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #1A3D5F;
    padding-top: 8px;
    padding-left:12px;
    padding-right:12px;
    padding-bottom:22px ;
    margin-bottom: 5px;
    @include m.tablet{
        height: 30px;
    }

}

.Text-Container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @include m.telefono{
        visibility: visible;
    }
    @include m.tablet{
        visibility: hidden;
    }
}

.Holder-container-img{
    padding-top: 70px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: white;
    
}

.descuento{
    padding-bottom: 5px;
}
.imgContainer{
    display: flex;
    justify-content: center;
    align-items: center;
   width: 360px;
   height: 160px; 

@include m.telefono{
    
}

@include m.tablet{
    height: 0px;
}
}

.imagen{
    margin-top: 15px;
    position: absolute;
    z-index: 1;
    
@include m.tablet{
    margin-top: 0;
    top: 5px;
    display: block;
}



}



